import { NextRequest, NextResponse } from 'next/server';
import { getServerUser, createServerSupabaseClient } from '@/lib/supabase/server';

interface AppPurchaseItem {
  id: string;
  receiptId: string;
  receiptNumber: string;
  receiptUrl?: string;
  amount: number;
  starCandyAmount: number;
  bonusAmount: number;
  paymentMethod: string; // Ï∂îÍ∞Ä
  paymentProvider: string; // Ï∂îÍ∞Ä
  platform: string; // 'ios' or 'android'
  storeProductId: string;
  transactionId: string;
  merchantTransactionId?: string; // Ï∂îÍ∞Ä
  status: string;
  currency: string;
  exchangeRate?: number; // Ï∂îÍ∞Ä
  originalAmount?: number; // Ï∂îÍ∞Ä
  originalCurrency?: string; // Ï∂îÍ∞Ä
  paymentDetails: { // Ï∂îÍ∞Ä
    cardLast4?: string;
    cardBrand?: string;
    bankName?: string;
    paypalEmail?: string;
  };
  receiptData: { // Ï∂îÍ∞Ä
    itemName: string;
    description: string;
    quantity: number;
    unitPrice: number;
    taxAmount?: number;
    discountAmount?: number;
  };
  metadata?: { // Ï∂îÍ∞Ä
    ipAddress?: string;
    userAgent?: string;
    referrer?: string;
  };
  createdAt: string;
  updatedAt: string; // Ï∂îÍ∞Ä
  receiptGeneratedAt?: string; // Ï∂îÍ∞Ä
  appStoreData: {
    originalTransactionId?: string; // Apple
    purchaseToken?: string; // Google Play
    orderId?: string; // Google Play
    bundleId?: string; // Apple
    packageName?: string; // Google Play
  };
}

interface RechargeResponse {
  success: boolean;
  data: AppPurchaseItem[];
  pagination: {
    page: number;
    limit: number;
    totalCount: number;
    totalPages: number;
    hasNext: boolean;
    hasPrevious: boolean;
  };
  statistics: {
    totalPurchases: number;
    totalAmount: number;
    totalStarCandy: number;
  };
  error?: string;
  message?: string;
}

export async function GET(request: NextRequest) {
  try {
    console.log('üì± [Recharge History API] Ïï±Íµ¨Îß§ ÏòÅÏàòÏ¶ù Ï°∞Ìöå ÏãúÏûë');

    // 1. ÏÇ¨Ïö©Ïûê Ïù∏Ï¶ù ÌôïÏù∏
    const user = await getServerUser();
    if (!user) {
      return NextResponse.json(
        { success: false, error: 'Unauthorized' },
        { status: 401 }
      );
    }

    // 2. ÏøºÎ¶¨ ÌååÎùºÎØ∏ÌÑ∞ ÌååÏã±
    const { searchParams } = new URL(request.url);
    const page = parseInt(searchParams.get('page') || '1', 10);
    const limit = parseInt(searchParams.get('limit') || '10', 10);
    const offset = (page - 1) * limit;

    console.log('üì± [Recharge History API] ÌååÎùºÎØ∏ÌÑ∞:', { 
      userId: user.id, 
      page, 
      limit, 
      offset 
    });

    // 3. Supabase ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ ÏÉùÏÑ±
    const supabase = await createServerSupabaseClient();

    // ÎîîÎ≤ÑÍπÖ: ÏÇ¨Ïö©ÏûêÏùò Î™®Îì† ÏòÅÏàòÏ¶ù Îç∞Ïù¥ÌÑ∞ ÌôïÏù∏
    console.log('üîç [DEBUG] ÏÇ¨Ïö©ÏûêÏùò Î™®Îì† ÏòÅÏàòÏ¶ù Îç∞Ïù¥ÌÑ∞ ÌôïÏù∏ ÏãúÏûë');
    
    const { data: allReceipts, error: allReceiptsError } = await supabase
      .from('receipts')
      .select('id, user_id, status, platform, created_at')
      .eq('user_id', user.id)
      .order('created_at', { ascending: false })
      .limit(20);
    
    console.log('üîç [DEBUG] ÏÇ¨Ïö©ÏûêÏùò Î™®Îì† ÏòÅÏàòÏ¶ù:', {
      count: allReceipts?.length || 0,
      receipts: allReceipts?.map(r => ({
        id: r.id,
        status: r.status,
        platform: r.platform,
        created_at: r.created_at
      })) || []
    });

    // ÎîîÎ≤ÑÍπÖ: Í∞Å Ï°∞Í±¥Î≥Ñ Îç∞Ïù¥ÌÑ∞ ÏàòÎüâ ÌôïÏù∏
    const { count: totalUserReceipts } = await supabase
      .from('receipts')
      .select('*', { count: 'exact' })
      .eq('user_id', user.id);
    
    const { count: completedReceipts } = await supabase
      .from('receipts')
      .select('*', { count: 'exact' })
      .eq('user_id', user.id)
      .eq('status', 'completed');
      
    const { count: appReceipts } = await supabase
      .from('receipts')
      .select('*', { count: 'exact' })
      .eq('user_id', user.id)
      .in('platform', ['ios', 'android']);

    console.log('üîç [DEBUG] Ï°∞Í±¥Î≥Ñ Îç∞Ïù¥ÌÑ∞ ÏàòÎüâ:', {
      totalUserReceipts,
      completedReceipts,
      appReceipts
    });

    // 4. Ïï±Íµ¨Îß§ ÏòÅÏàòÏ¶ù Ï°∞Ìöå - Ïä§ÎßàÌä∏Ìïú Ï°∞Í±¥ ÏÑ†ÌÉù
    let receipts: any[] | null = null;
    let receiptsError: any = null;
    let count: number | null = null;
    
    console.log('üîç [DEBUG] ÏÇ¨Ïö©Ïûê ÏòÅÏàòÏ¶ù Ï°∞Ìöå ÏãúÏûë:', {
      userId: user.id,
      offset,
      limit,
      completedReceipts,
      totalUserReceipts
    });
    
    // Î®ºÏ†Ä completed ÏÉÅÌÉúÍ∞Ä Ï∂©Î∂ÑÌïúÏßÄ ÌôïÏù∏
    if (completedReceipts && completedReceipts >= offset + 1) {
      // completed ÏÉÅÌÉúÎ°ú Ï∂©Î∂ÑÌïú Îç∞Ïù¥ÌÑ∞Í∞Ä ÏûàÎäî Í≤ΩÏö∞ÏóêÎßå completed Ï°∞Í±¥ ÏÇ¨Ïö©
      const result = await supabase
        .from('receipts')
        .select('*', { count: 'exact' })
        .eq('user_id', user.id)
        .eq('status', 'completed')
        .in('platform', ['ios', 'android', 'web'])
        .order('created_at', { ascending: false })
        .range(offset, offset + limit - 1);
      
      receipts = result.data;
      receiptsError = result.error;
      count = result.count;
      
      console.log('‚úÖ [DEBUG] completed ÏÉÅÌÉú Ï°∞Í±¥ÏúºÎ°ú Ï°∞Ìöå:', {
        count: receipts?.length || 0,
        totalCount: count
      });
    } else {
      // completed Îç∞Ïù¥ÌÑ∞Í∞Ä Î∂ÄÏ°±ÌïòÎ©¥ Ï≤òÏùåÎ∂ÄÌÑ∞ Ï°∞Í±¥ ÏôÑÌôî
      console.log('‚ö†Ô∏è [DEBUG] completed Îç∞Ïù¥ÌÑ∞ Î∂ÄÏ°±, Ï°∞Í±¥ ÏôÑÌôîÌïòÏó¨ Ï°∞Ìöå');
      
      const result = await supabase
        .from('receipts')
        .select('*', { count: 'exact' })
        .eq('user_id', user.id)
        .in('platform', ['ios', 'android', 'web'])
        .order('created_at', { ascending: false })
        .range(offset, offset + limit - 1);
      
      receipts = result.data;
      receiptsError = result.error;
      count = result.count;
      
      console.log('‚úÖ [DEBUG] ÏÉÅÌÉú Ï°∞Í±¥ ÏôÑÌôîÌïòÏó¨ Ï°∞Ìöå:', {
        count: receipts?.length || 0,
        totalCount: count
      });
    }

    if (receiptsError) {
      console.error('‚ùå [Recharge History API] ÏòÅÏàòÏ¶ù Ï°∞Ìöå Ïã§Ìå®:', receiptsError);
      return NextResponse.json(
        { 
          success: false, 
          error: 'Failed to fetch recharge history',
          message: receiptsError.message 
        },
        { status: 500 }
      );
    }

    console.log('‚úÖ [Recharge History API] ÏøºÎ¶¨ Í≤∞Í≥º:', {
      count: receipts?.length || 0,
      totalCount: count
    });

    // 5. ÏµúÏ¢Ö Í≤∞Í≥º ÏÑ§Ï†ï
    let finalReceipts = receipts;
    let finalCount = count;

    // 5.5. Products Ï†ïÎ≥¥ Í∞ÄÏ†∏Ïò§Í∏∞ (Î≥ÑÎèÑ ÏøºÎ¶¨)
    const productIds = Array.from(new Set((finalReceipts || []).map(r => r.product_id).filter((id): id is string => Boolean(id))));
    let productsMap = new Map();
    
    console.log('üè∑Ô∏è [DEBUG] Product IDs found in receipts:', productIds);
    
    if (productIds.length > 0) {
      const { data: productsData, error: productsError } = await supabase
        .from('products')
        .select('id, product_name, star_candy, star_candy_bonus, web_price_krw, web_price_usd, price')
        .in('id', productIds);
      
      console.log('üè∑Ô∏è [DEBUG] Products query result:', {
        error: productsError,
        count: productsData?.length || 0,
        products: productsData?.map(p => ({
          id: p.id,
          name: p.product_name,
          price: p.price,
          web_price_krw: p.web_price_krw,
          web_price_usd: p.web_price_usd,
          star_candy: p.star_candy,
          star_candy_bonus: p.star_candy_bonus
        })) || []
      });
      
      if (productsData) {
        productsData.forEach(product => {
          productsMap.set(product.id, product);
        });
      }
    }

    // 6. Îç∞Ïù¥ÌÑ∞ Î≥ÄÌôò (Ïï±Íµ¨Îß§ ÌòïÌÉúÎ°ú)
    const rechargeItems: AppPurchaseItem[] = (finalReceipts || []).map((receipt) => {
      // ÏòÅÏàòÏ¶ù Îç∞Ïù¥ÌÑ∞ ÌååÏã± (Í∞úÏÑ†Îêú ÏïàÏ†ÑÏÑ±)
      let receiptData: any = {};
      try {
        if (typeof receipt.receipt_data === 'string') {
          // Î¨∏ÏûêÏó¥Ïù¥ ÎπÑÏñ¥ÏûàÍ±∞ÎÇò Ïú†Ìö®ÌïòÏßÄ ÏïäÏùÄ JSONÏù∏ÏßÄ ÌôïÏù∏
          const trimmedData = receipt.receipt_data.trim();
          if (trimmedData === '' || trimmedData === 'null' || trimmedData === 'undefined') {
            receiptData = {};
          } else {
            // JSON Î¨∏ÏûêÏó¥Ïù∏ÏßÄ ÌôïÏù∏ (Ï≤´ Î¨∏ÏûêÍ∞Ä { ÎòêÎäî [Î°ú ÏãúÏûëÌïòÎäîÏßÄ)
            if (trimmedData.startsWith('{') || trimmedData.startsWith('[')) {
              receiptData = JSON.parse(trimmedData);
            } else {
              console.warn('Ïú†Ìö®ÌïòÏßÄ ÏïäÏùÄ JSON ÌòïÌÉú:', trimmedData.substring(0, 50));
              receiptData = {};
            }
          }
        } else if (receipt.receipt_data && typeof receipt.receipt_data === 'object') {
          receiptData = receipt.receipt_data;
        } else {
          receiptData = {};
        }
      } catch (e) {
        console.warn('ÏòÅÏàòÏ¶ù Îç∞Ïù¥ÌÑ∞ ÌååÏã± Ïã§Ìå®:', {
          error: e instanceof Error ? e.message : 'Unknown error',
          data: typeof receipt.receipt_data === 'string' 
            ? receipt.receipt_data.substring(0, 100) 
            : typeof receipt.receipt_data
        });
        receiptData = {};
      }
      
      // ÌååÏã±Îêú receiptData Íµ¨Ï°∞ Î°úÍ∑∏ (Ï≤òÏùå 5Í∞úÎßå)
      if (receipt.id <= (finalReceipts?.[4]?.id || 5)) {
        console.log(`üí≥ [DEBUG] Receipt ${receipt.id} parsed data:`, {
          platform: receipt.platform,
          product_id: receipt.product_id,
          receiptDataKeys: Object.keys(receiptData),
          receiptDataSample: {
            amount: receiptData.amount,
            quantity: receiptData.quantity,
            currency: receiptData.currency,
            payment_method: receiptData.payment_method,
            star_candy_amount: receiptData.star_candy_amount,
            starCandy: receiptData.starCandy,
            bonus_amount: receiptData.bonus_amount
          }
        });
      }

      // Í≤ÄÏ¶ù Îç∞Ïù¥ÌÑ∞ ÌååÏã±
      let verificationData: any = {};
      try {
        verificationData = typeof receipt.verification_data === 'object'
          ? receipt.verification_data
          : JSON.parse(String(receipt.verification_data || '{}'));
      } catch (e) {
        console.warn('Í≤ÄÏ¶ù Îç∞Ïù¥ÌÑ∞ ÌååÏã± Ïã§Ìå®:', e);
      }

      // Products ÌÖåÏù¥Î∏î Ï†ïÎ≥¥ 
      const productInfo = productsMap.get(receipt.product_id) || {};
      
      // ÌîåÎû´ÌèºÎ≥Ñ Îç∞Ïù¥ÌÑ∞ Ï≤òÎ¶¨
      const isIOS = receipt.platform === 'ios';
      const isAndroid = receipt.platform === 'android';
      const isWeb = receipt.platform === 'web';
      
      // Î≥ÑÏÇ¨ÌÉï ÏàòÎüâ Í≥ÑÏÇ∞ (Products ÌÖåÏù¥Î∏î Ïö∞ÏÑ†, Í∏∞Î≥∏Í∞í ÏÑ§Ï†ï)
      let baseStarCandy = Number(productInfo.star_candy || receiptData.star_candy_amount || receiptData.starCandy || 0);
      let bonusStarCandy = Number(productInfo.star_candy_bonus || receiptData.bonus_amount || receiptData.bonusAmount || 0);
      
      // Î≥ÑÏÇ¨ÌÉïÏù¥ 0Ïù¥Î©¥ ÏÉÅÌíà IDÏóê Îî∞Î•∏ Í∏∞Î≥∏Í∞í ÏÑ§Ï†ï
      if (baseStarCandy === 0) {
        const defaultStarCandy: { [key: string]: number } = {
          'STAR100': 100,
          'STAR300': 300,
          'STAR500': 500,
          'STAR1000': 1000,
          'STAR3000': 3000,
          'STAR5000': 5000
        };
        baseStarCandy = defaultStarCandy[receipt.product_id || ''] || 100;
      }
      
      // Í≤∞Ï†ú Í∏àÏï° Í≥ÑÏÇ∞ - receiptData Í∏∞Î∞ò
      const quantity = Number(receiptData.quantity || 1);
      
      // 1ÏàúÏúÑ: receiptData.amount ÏÇ¨Ïö©
      let actualAmount = Number(receiptData.amount || 0);
      
      // 2ÏàúÏúÑ: verification_dataÏóêÏÑú ÌôïÏù∏ (iOS/Android)
      if (actualAmount === 0 && verificationData && (isIOS || isAndroid)) {
        if (isIOS && verificationData.price) {
          actualAmount = Number(verificationData.price) / 1000000; // ÎßàÏù¥ÌÅ¨Î°ú Îã®ÏúÑ Î≥ÄÌôò
        } else if (isAndroid && verificationData.priceAmountMicros) {
          actualAmount = Number(verificationData.priceAmountMicros) / 1000000;
        }
              }
      
      // 3ÏàúÏúÑ: Products ÌÖåÏù¥Î∏îÏóêÏÑú ÌôïÏù∏ (Ïõπ Í≤∞Ï†úÏö©)
      if (actualAmount === 0 && isWeb) {
        const currency = receiptData.currency || 'KRW';
        if (currency === 'USD') {
          actualAmount = Number(productInfo.web_price_usd || 0) * quantity;
        } else {
          actualAmount = Number(productInfo.web_price_krw || 0) * quantity;
        }
      }
      
      // 4ÏàúÏúÑ: Í∏∞Î≥∏Í∞í ÏÑ§Ï†ï
      if (actualAmount === 0) {
        const defaultAmounts: { [key: string]: number } = {
          'STAR100': 1100,
          'STAR300': 3300, 
          'STAR500': 5500,
          'STAR1000': 11000,
          'STAR3000': 33000,
          'STAR5000': 55000
        };
        actualAmount = (defaultAmounts[receipt.product_id || ''] || 1000) * quantity;
        console.log(`‚ö†Ô∏è [DEBUG] Í∏∞Î≥∏Í∞í ÏÇ¨Ïö©: ${receipt.product_id} -> ${actualAmount}Ïõê`);
      }
      
      // Îã®Í∞Ä Í≥ÑÏÇ∞ (Í≤∞Ï†ú Í∏àÏï° √∑ ÏàòÎüâ)
      const unitPrice = Math.round(actualAmount / quantity);
      
      // Ï≤òÏùå 3Í∞ú ÏòÅÏàòÏ¶ùÏóê ÎåÄÌï¥ ÏÉÅÏÑ∏ Î°úÍ∑∏
      if (receipt.id <= 3) {
        console.log(`üí∞ [DEBUG] Receipt ${receipt.id} Í∏àÏï° Í≥ÑÏÇ∞:`, {
          productId: receipt.product_id,
          platform: receipt.platform,
          receiptDataAmount: receiptData.amount,
          verificationPrice: verificationData?.price || verificationData?.priceAmountMicros,
          quantity: quantity,
          finalAmount: actualAmount,
          unitPrice: unitPrice
        });
      }
      
      // Í≤∞Ï†ú Î∞©Î≤ï Í≤∞Ï†ï
      const paymentMethod = isIOS ? 'apple_store' : isAndroid ? 'google_play' : isWeb ? 'web_payment' : 'app_store';
      const paymentProvider = isIOS ? 'Apple App Store' : isAndroid ? 'Google Play Store' : isWeb ? (receiptData.payment_method === 'port_one' ? 'Port One' : 'PayPal') : 'App Store';
      
      // ÏÉÅÌíà Ï†ïÎ≥¥ (Products ÌÖåÏù¥Î∏î Ïö∞ÏÑ†)
      const itemName = productInfo.product_name || receiptData.product_name || receiptData.item_name || 'Î≥ÑÏÇ¨ÌÉï Íµ¨Îß§';
      const description = receiptData.description || 'Î≥ÑÏÇ¨ÌÉï Íµ¨Îß§';
      
      return {
        id: receipt.id.toString(),
        receiptId: receipt.receipt_hash || `APP_${receipt.id}`,
        receiptNumber: `A${new Date(receipt.created_at || new Date()).getFullYear()}${('000000' + receipt.id.toString()).slice(-6)}`,
        receiptUrl: receiptData.receipt_url || undefined,
        amount: actualAmount,
        starCandyAmount: Number(baseStarCandy),
        bonusAmount: Number(bonusStarCandy),
        paymentMethod,
        paymentProvider,
        platform: receipt.platform,
        storeProductId: String(receipt.product_id || productInfo.id || receiptData.product_id || receiptData.store_product_id || 'unknown'),
        transactionId: isIOS 
          ? (verificationData.transaction_id || receiptData.original_transaction_id || receipt.receipt_hash)
          : (receiptData.order_id || receiptData.purchase_token || receipt.receipt_hash),
        merchantTransactionId: receiptData.merchant_transaction_id || undefined,
        status: receipt.status,
        currency: receiptData.currency || 'KRW',
        exchangeRate: receiptData.exchange_rate ? Number(receiptData.exchange_rate) : undefined,
        originalAmount: receiptData.original_amount ? Number(receiptData.original_amount) : undefined,
        originalCurrency: receiptData.original_currency || undefined,
        paymentDetails: {
          cardLast4: receiptData.card_last4 || undefined,
          cardBrand: receiptData.card_brand || undefined,
          bankName: receiptData.bank_name || undefined,
          paypalEmail: receiptData.paypal_email || undefined,
        },
        receiptData: {
          itemName,
          description,
          quantity: quantity,
          unitPrice: unitPrice,
          taxAmount: receiptData.tax_amount ? Number(receiptData.tax_amount) : undefined,
          discountAmount: receiptData.discount_amount ? Number(receiptData.discount_amount) : undefined,
        },
        metadata: {
          ipAddress: receiptData.ip_address || undefined,
          userAgent: receiptData.user_agent || undefined,
          referrer: receiptData.referrer || undefined,
        },
        createdAt: receipt.created_at || new Date().toISOString(),
        updatedAt: receipt.created_at || new Date().toISOString(), // updated_at ÌïÑÎìúÍ∞Ä ÏóÜÏúºÎØÄÎ°ú created_at ÏÇ¨Ïö©
        receiptGeneratedAt: receiptData.receipt_generated_at || receipt.created_at || undefined,
        appStoreData: {
          // Apple App Store Îç∞Ïù¥ÌÑ∞
          ...(isIOS && {
            originalTransactionId: verificationData.original_transaction_id || receiptData.original_transaction_id,
            bundleId: receiptData.bundle_id || verificationData.bundle_id,
          }),
          // Google Play Store Îç∞Ïù¥ÌÑ∞
          ...(isAndroid && {
            purchaseToken: receiptData.purchase_token || verificationData.purchase_token,
            orderId: receiptData.order_id || verificationData.order_id,
            packageName: receiptData.package_name || verificationData.package_name,
          }),
        },
      };
    });

    // 7. Ï†ÑÏ≤¥ ÌÜµÍ≥Ñ Í≥ÑÏÇ∞ (Î≥ÑÎèÑ ÏøºÎ¶¨)
    let statistics = {
      totalPurchases: finalCount || 0,
      totalAmount: 0,
      totalStarCandy: 0,
    };

    console.log('üéØ [STATS] ÌÜµÍ≥Ñ Í≥ÑÏÇ∞ ÏãúÏûë - ÏßÑÏûÖÏ†ê ÌôïÏù∏:', {
      userId: user.id,
      userEmail: user.email,
      timestamp: new Date().toISOString()
    });

    try {
      console.log('üîÑ [STATS] Step 1: ÏÇ¨Ïö©Ïûê Ï†ÑÏ≤¥ Îç∞Ïù¥ÌÑ∞ Ï°∞Ìöå ÏãúÏûë');
      
      // Î®ºÏ†Ä Ïù¥ ÏÇ¨Ïö©ÏûêÏùò Î™®Îì† Îç∞Ïù¥ÌÑ∞Í∞Ä ÏûàÎäîÏßÄ ÌôïÏù∏
      const { data: allUserData, error: allUserError } = await supabase
        .from('receipts')
        .select('id, product_id, platform, status, created_at')
        .eq('user_id', user.id);
        
      if (allUserError) {
        console.error('‚ùå [STATS] Step 1 Ïã§Ìå®:', allUserError);
        throw allUserError;
      }
      
      console.log('‚úÖ [STATS] Step 1 ÏôÑÎ£å:', { count: allUserData?.length || 0 });
        
      console.log('üîç [TOTAL DEBUG] ÏÇ¨Ïö©ÏûêÏùò Ï†ÑÏ≤¥ ÏòÅÏàòÏ¶ù Îç∞Ïù¥ÌÑ∞:', {
        userId: user.id,
        totalCount: allUserData?.length || 0,
        error: allUserError,
        sampleData: allUserData?.slice(0, 3).map(r => ({
          id: r.id,
          status: r.status,
          platform: r.platform,
          product_id: r.product_id,
          created_at: r.created_at
        })) || []
      });
      
      console.log('üîÑ [STATS] Step 2: ÌÜµÍ≥ÑÏö© ÏòÅÏàòÏ¶ù Ï°∞Ìöå ÏãúÏûë');
      
      // Ï†ÑÏ≤¥ ÏòÅÏàòÏ¶ù Ï°∞Ìöå (ÌÜµÍ≥ÑÏö©) - amount Ïª¨ÎüºÎ™Ö Ï†úÍ±∞ÌïòÍ≥† Í∏∞Î≥∏ Ïª¨ÎüºÎì§Îßå Ï°∞Ìöå
      const { data: allReceipts, error: receiptsError } = await supabase
        .from('receipts')
        .select('id, product_id, receipt_data, platform, status, created_at')
        .eq('user_id', user.id);
        // .in('platform', ['ios', 'android', 'web']); // ÌîåÎû´Ìèº ÌïÑÌÑ∞ Ï†úÍ±∞
        
      if (receiptsError) {
        console.error('‚ùå [STATS] Step 2 Ïã§Ìå®:', receiptsError);
        throw receiptsError;
      }
      
      console.log('‚úÖ [STATS] Step 2 ÏôÑÎ£å:', { count: allReceipts?.length || 0 });
        
      console.log('üìä [DEBUG] ÌÜµÍ≥ÑÏö© ÌîåÎû´Ìèº ÌïÑÌÑ∞ÎßÅÎêú ÏòÅÏàòÏ¶ù Ï°∞Ìöå:', {
        count: allReceipts?.length || 0,
        statusBreakdown: allReceipts?.reduce((acc: any, r) => {
          acc[r.status] = (acc[r.status] || 0) + 1;
          return acc;
        }, {}) || {},
        platforms: allReceipts?.reduce((acc: any, r) => {
          acc[r.platform] = (acc[r.platform] || 0) + 1;
          return acc;
        }, {}) || {}
      });

      console.log('üìä [STATS] ÌÜµÍ≥Ñ Í≥ÑÏÇ∞ ÏãúÏûë:', {
        totalReceipts: allReceipts?.length || 0,
        hasReceipts: !!allReceipts,
        isArray: Array.isArray(allReceipts)
      });

      if (allReceipts && Array.isArray(allReceipts) && allReceipts.length > 0) {
        // Ï†ÑÏ≤¥ ÏòÅÏàòÏ¶ùÏóêÏÑú ÌÜµÍ≥Ñ Í≥ÑÏÇ∞
        let totalAmount = 0;
        let totalStarCandy = 0;
        let completedCount = 0;
        let processedCount = 0;

        for (const receipt of allReceipts) {
          processedCount++;
          
          console.log(`üîÑ [STATS] Ï≤òÎ¶¨ Ï§ë ${processedCount}/${allReceipts.length}:`, {
            receiptId: receipt.id,
            status: receipt.status,
            platform: receipt.platform,
            productId: receipt.product_id
          });
          // ÏùºÎã® Î™®Îì† ÏÉÅÌÉúÏùò ÏòÅÏàòÏ¶ùÏùÑ Ìè¨Ìï®Ìï¥ÏÑú ÌÜµÍ≥Ñ Í≥ÑÏÇ∞Ìï¥Î≥¥Í∏∞ (ÎîîÎ≤ÑÍπÖÏö©)
          // if (receipt.status !== 'completed') {
          //   continue;
          // }
          
          let receiptData: any = {};
          try {
            if (typeof receipt.receipt_data === 'string') {
              const trimmedData = receipt.receipt_data.trim();
              if (trimmedData === '' || trimmedData === 'null' || trimmedData === 'undefined') {
                receiptData = {};
              } else if (trimmedData.startsWith('{') || trimmedData.startsWith('[')) {
                receiptData = JSON.parse(trimmedData);
              } else {
                receiptData = {};
              }
            } else if (receipt.receipt_data && typeof receipt.receipt_data === 'object') {
              receiptData = receipt.receipt_data;
            } else {
              receiptData = {};
            }
          } catch (e) {
            receiptData = {};
            continue;
          }

          const productInfo = productsMap.get(receipt.product_id) || {};
          const isWeb = receipt.platform === 'web';
          const statsQuantity = Number(receiptData.quantity || 1);
          
          // Ïã§Ï†ú Í≤∞Ï†ú Í∏àÏï° Í≥ÑÏÇ∞ - receiptDataÏôÄ Í∏∞Î≥∏Í∞í Í∏∞Î∞ò
          let itemAmount = 0;
          
          // 1ÏàúÏúÑ: receiptData.amount ÏÇ¨Ïö©  
          if (receiptData.amount && !isNaN(Number(receiptData.amount))) {
            itemAmount = Number(receiptData.amount);
          }
          
          // 2ÏàúÏúÑ: ÏÉÅÌíà IDÏóê Îî∞Î•∏ Í∏∞Î≥∏Í∞í ÏÑ§Ï†ï
          if (itemAmount === 0 || isNaN(itemAmount)) {
            const defaultAmounts: { [key: string]: number } = {
              'STAR100': 1100,
              'STAR300': 3300, 
              'STAR500': 5500,
              'STAR1000': 11000,
              'STAR3000': 33000,
              'STAR5000': 55000
            };
            itemAmount = (defaultAmounts[receipt.product_id || ''] || 1100) * statsQuantity;
          }
          
          console.log(`üí∞ [STATS DEBUG] Receipt ${receipt.id} ÌÜµÍ≥Ñ Í∏àÏï°:`, {
            receiptDataAmount: receiptData.amount,
            defaultUsed: !receiptData.amount,
            finalAmount: itemAmount,
            productId: receipt.product_id,
            quantity: statsQuantity
          });
          
          totalAmount += itemAmount;
          completedCount++;

          // Î≥ÑÏÇ¨ÌÉï ÏàòÎüâ Í≥ÑÏÇ∞
          let baseStarCandy = 0;
          let bonusStarCandy = 0;
          
          // 1ÏàúÏúÑ: Products ÌÖåÏù¥Î∏îÏóêÏÑú
          if (productInfo.star_candy) {
            baseStarCandy = Number(productInfo.star_candy);
          }
          
          // 2ÏàúÏúÑ: receiptDataÏóêÏÑú
          if (baseStarCandy === 0) {
            if (receiptData.star_candy_amount) {
              baseStarCandy = Number(receiptData.star_candy_amount);
            } else if (receiptData.starCandy) {
              baseStarCandy = Number(receiptData.starCandy);
            }
          }
          
          // 3ÏàúÏúÑ: ÏÉÅÌíà IDÏóê Îî∞Î•∏ Í∏∞Î≥∏Í∞í
          if (baseStarCandy === 0 || isNaN(baseStarCandy)) {
            const defaultStarCandy: { [key: string]: number } = {
              'STAR100': 100,
              'STAR300': 300,
              'STAR500': 500,
              'STAR1000': 1000,
              'STAR3000': 3000,
              'STAR5000': 5000
            };
            baseStarCandy = defaultStarCandy[receipt.product_id || ''] || 100;
          }
          
          // Î≥¥ÎÑàÏä§ Î≥ÑÏÇ¨ÌÉï
          if (productInfo.star_candy_bonus) {
            bonusStarCandy = Number(productInfo.star_candy_bonus);
          } else if (receiptData.bonus_amount) {
            bonusStarCandy = Number(receiptData.bonus_amount);
          } else if (receiptData.bonusAmount) {
            bonusStarCandy = Number(receiptData.bonusAmount);
          }
          
          // NaN Ï≤¥ÌÅ¨
          if (isNaN(bonusStarCandy)) {
            bonusStarCandy = 0;
          }
          
          console.log(`‚≠ê [STATS DEBUG] Receipt ${receipt.id} Î≥ÑÏÇ¨ÌÉï:`, {
            productStarCandy: productInfo.star_candy,
            receiptStarCandy: receiptData.star_candy_amount || receiptData.starCandy,
            baseStarCandy,
            bonusStarCandy,
            total: baseStarCandy + bonusStarCandy
          });
          
          totalStarCandy += baseStarCandy + bonusStarCandy;
        }

        statistics = {
          totalPurchases: completedCount,
          totalAmount,
          totalStarCandy,
        };
        
        console.log('üìä [DEBUG] ÏµúÏ¢Ö ÌÜµÍ≥Ñ Í≥ÑÏÇ∞ ÏôÑÎ£å:', {
          totalReceipts: allReceipts.length,
          processedReceipts: processedCount,
          completedReceipts: completedCount,
          totalAmount,
          totalStarCandy
        });
      } else {
        console.log('‚ö†Ô∏è [STATS] ÌÜµÍ≥Ñ Í≥ÑÏÇ∞Ìï† ÏòÅÏàòÏ¶ùÏù¥ ÏóÜÏùå:', {
          allReceiptsLength: allReceipts?.length || 0,
          isArray: Array.isArray(allReceipts),
          allReceiptsType: typeof allReceipts
        });
      }
    } catch (error) {
      console.error('‚ùå [STATS] ÌÜµÍ≥Ñ Í≥ÑÏÇ∞ Ïã§Ìå®:', error);
      console.error('‚ùå [STATS] ÏóêÎü¨ Ïä§ÌÉù:', error instanceof Error ? error.stack : 'No stack trace');
      
      // ÌéòÏù¥ÏßÄ Îç∞Ïù¥ÌÑ∞Î°ú fallback
      console.log('üîÑ [STATS] Fallback Í≥ÑÏÇ∞ ÏãúÏûë - ÌòÑÏû¨ ÌéòÏù¥ÏßÄ Îç∞Ïù¥ÌÑ∞ ÏÇ¨Ïö©');
      
      const fallbackTotalAmount = rechargeItems.reduce((sum, item) => {
        const amount = Number(item.amount) || 0;
        console.log(`üí∞ [FALLBACK] Item ${item.id}: ${amount}Ïõê`);
        return sum + (isNaN(amount) ? 0 : amount);
      }, 0);
      
      const fallbackTotalStarCandy = rechargeItems.reduce((sum, item) => {
        const starCandy = Number(item.starCandyAmount) || 0;
        const bonus = Number(item.bonusAmount) || 0;
        console.log(`‚≠ê [FALLBACK] Item ${item.id}: Í∏∞Î≥∏ ${starCandy} + Î≥¥ÎÑàÏä§ ${bonus}`);
        return sum + (isNaN(starCandy) ? 0 : starCandy) + (isNaN(bonus) ? 0 : bonus);
      }, 0);
      
      statistics = {
        totalPurchases: finalCount || 0,
        totalAmount: fallbackTotalAmount,
        totalStarCandy: fallbackTotalStarCandy,
      };
      
      console.log('üîÑ [STATS] Fallback ÌÜµÍ≥Ñ Í≥ÑÏÇ∞ ÏôÑÎ£å:', statistics);
    }

    // 8. ÌéòÏù¥ÏßÄÎÑ§Ïù¥ÏÖò Ï†ïÎ≥¥
    const totalPages = Math.ceil((finalCount || 0) / limit);
    const pagination = {
      page,
      limit,
      totalCount: finalCount || 0,
      totalPages,
      hasNext: page < totalPages,
      hasPrevious: page > 1,
    };

    // ÏµúÏ¢Ö ÌÜµÍ≥Ñ ÏïàÏ†ÑÏÑ± Í≤ÄÏ¶ù
    const safeStatistics = {
      totalPurchases: Number(statistics.totalPurchases) || 0,
      totalAmount: Number(statistics.totalAmount) || 0,
      totalStarCandy: Number(statistics.totalStarCandy) || 0,
    };
    
    console.log('üìä [Recharge History API] ÏùëÎãµ Ï§ÄÎπÑ ÏôÑÎ£å:', {
      itemsCount: rechargeItems.length,
      originalStatistics: statistics,
      safeStatistics,
      pagination
    });

    const response: RechargeResponse = {
      success: true,
      data: rechargeItems,
      pagination,
      statistics: safeStatistics,
    };

    return NextResponse.json(response);

  } catch (error) {
    console.error('‚ùå [Recharge History API] ÏòàÏÉÅÏπò Î™ªÌïú Ïò§Î•ò:', error);
    return NextResponse.json(
      { 
        success: false, 
        error: 'Internal server error',
        message: error instanceof Error ? error.message : 'Unknown error'
      },
      { status: 500 }
    );
  }
} 